.banner {
    background-image: url("https://res.cloudinary.com/dwcrcaa7u/image/upload/v1688315737/BZZWorld%20Shop/coverpng_vstcya.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vmin;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: black;
  }
  
  .banner > h1 {
    margin: 5vmax;
  
    font: 800 5.5vmax "Roboto";
  }
  
  .banner > p {
    width: 800px;
    font: 500 2.4vmax "Lucida Sans";
    
  }
  
  .banner > a > button {
    margin-bottom: 19vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white;
    border-radius: 0;
    padding: 1vmax;
    transition: all 0.5s;
    width: 9vmax;
    font: 500 1vmax "Roboto";
  }
  .banner > a > button:hover {
    background-color: rgba(255, 255, 255, 0);
    color: white;
  }
  
  .banner::after {
    content: "";
    width: 100vw;
    height: 100vmin;
    background-color: #ffffff;
    position: absolute;
    top: 0%;
    left: 0;
    -webkit-clip-path: polygon(100% 68%, 0 100%, 100% 100%);
            clip-path: polygon(100% 68%, 0 100%, 100% 100%);
    max-width: 100%;
  }
  
